import * as React from "react";
import { connect } from "react-redux";

import { selectSquare } from "../redux/Actions";
import { GameState } from "../redux/GameState";

interface SquareProps { key: number; value : string, selectSquare : any}

class Square extends React.Component<SquareProps, {}> {
    handleClick = () => {
        this.props.selectSquare(this.props.key);
    }

    render() {
        return (
            <button className="square" onClick={this.handleClick}>
                {this.props.value}
            </button>
        );
    }
}

const mapStateToProps = (state: GameState) => {
    return {
        value: state.history[state.history.length - 1].squares[this.props.key]
    };
}

export default connect(mapStateToProps,{selectSquare})(Square);
